.postsCards{
    margin-bottom: 3rem;
    margin: auto;
   display: flex;
   flex-direction: column;
   gap: 1rem;
   margin-bottom: 3rem;
   border: 1px solid rgba(0, 0, 0, 0.185);
   padding: 2rem;
   border-radius: 20px;

}

.postImageBox{
 width: 100%;

    overflow: hidden;
    
}
.postImage{
width: 100%;
height: 100%;
max-height: 30rem;
object-fit: cover;
aspect-ratio: 1/1;
 object-position: center;
   
}

.createdBy{
    font-weight: 500;
}

.tagsContainer{
 display: flex;
gap: .5rem;

}
.tags{
    border: 1px solid rgba(0, 0, 0, 0.281);
    padding: 2px;
    border-radius: 5px;
    font-size: 1.3rem;
    font-weight: bold;
}
.linkBtn{
    border: 1px solid black;
    max-width: 120px;
    text-align: center;
    background-color: #000;
    color: #fff;
}