.headerContent{
display: flex;
justify-content: space-between;
align-items: center;
padding:20px 3.5rem;
position: relative;

}


.mainMenu{  
align-items: center;
display:flex;
gap: 2rem;
position: relative;
width: 60rem;
min-height: 50px;
}


.logoff{
 background:   #D1B645
}




.responsiveMenu{
display: none;
z-index: 2;
cursor: pointer;    
height: 30px;
width:30px;
position: absolute;
left: 5px;
top: 3rem;
}
.check{display: none;}

.burguer{
height: 3px;
width: 100%;
background-color: #000;
position: absolute;
}

.burguer::before,.burguer:after{
    content: '';
    height: 3px;
width: 100%;
background-color: #000;
position: absolute;

    
}

.burguer::before{
margin-top: 10px;
}
.burguer::after{
margin-top: 20px;
}





@media (max-width:874px){
    .headerContent{
        flex-direction: column;
        justify-content: center;
        align-items:center;
        gap: 2rem;
    }
}


@media (max-width:768px){

   
.check:checked + .mainMenu{left: 0;}


.mainMenu{

background-color: #4d79e8ce;
border-radius: 5px;
gap:6rem;
position: absolute;
top: 100%;
left: -100%;
height: 60vh;
flex-direction: column;
justify-content: center;
padding: 2rem;
width: 30rem;
transition: left .5s linear;
}
.responsiveMenu{display: block;}


}


