@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400;1,500&display=swap');

*{
margin: 0;
padding: 0;
box-sizing: border-box;
}
:root{
--global-fonts:'Montserrat'
}
html{
height: 100%;
font-size: 62.5%;
}
body{
font-size: 1.6rem;
font-family: var(--global-fonts);
}

::before,::after{
box-sizing: border-box;
}

li{
list-style: none;

}
a{
color: #000;
text-decoration: none;
padding:10px 15px;

}
.active{
animation: jump .1s cubic-bezier(1, 1.46, 1, 1.49);
background-color: #4449D1;
border-radius: 20px;
color: #fff;
display: block;
}

.error{
    background-color:#913d16bd;
    padding:0px 5px;
    font-size: 1.4rem;
    color: #fff;
    text-align: center;
}

@keyframes jump{
0%{
transform: scale(1);
}
50%{
transform: scale(1.05);
}
100%{
transform: scale(1);
}
}


.btn{
padding: 10px 15px;
background-color: #185585;
border-radius: 20px;
border: none;
color: #fff;
font-weight: 500;
font-size: 1.5rem;
font-family: var(--global-fonts);
cursor:pointer
}

.btn:hover{
transform: scale(1.03);

}

@media (max-width:600px){
    html{
        font-size: 53%;
    }
}