.dashboard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.212);
    padding: 2rem;
    gap: 1.2rem;
}

.postContainer{
display: flex;
justify-content: center;
flex-direction: column;
gap: 1.2rem;
border: 1px solid rgba(0, 0, 0, 0.15);
padding: 2rem;
border-radius: 20px;
text-align: left;

}

.imgContainer img{
object-fit: cover;
max-height:50rem;
width: 45rem;
overflow: hidden;

}
.controls{
display: flex;
gap: 1rem;
}
.normalBtn,.deleteBtn{
padding: 1.1rem;
font-size: 1.5rem;
font-family: var(--global-fonts);
border: 1px solid rgba(0, 0, 0, 0.425);
cursor: pointer;
}


.normalBtn:hover{
    background-color: rgb(179, 177, 177);
}
.deleteBtn:hover{
    background-color: red;
    color: #fff;

}


.noPost{
    text-align: center;
    font-weight: bold;
    font-size: 2rem;
}

@media(max-width: 460px){
    .imgContainer img{
        width: 100%;
    }
}