.home{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
padding: 2rem;
gap: 3rem;
}
.searchForm{
display: flex;
align-items: center;
gap: 10px;
width:100%;
max-width: 700px;
}
.searchBox{
    
    gap: 10px;
    width: 100%;
   
 
 
}
.searchInput:focus::placeholder{
opacity: 0;


}


.searchBar{
width: 100%;
padding: 10px;
font-family: var(--global-fonts);
font-size: 1.5rem;
border-radius: 20px;
}

.btn{
    background-color: #030d68;
    border-radius: 20px;
    border: none;
    color: #fff;
    cursor: pointer;
    padding: 10px 15px;
    font-size: 1.5rem;
    font-family: var(--global-fonts);

}

.homePosts{
    margin-top: 10rem;
}