.postsContainer{
display:flex;
justify-content: center;
align-items: center;
flex-direction: column;

}
.title{
font-size: 2.5rem;
}
.formContainer{
margin-top: 5rem;
}

.postForm{
display: flex;
flex-direction: column;
gap: 1rem;
padding: 5px;

}
.postForm .controlInputs{
width: 100%;
display: flex;
flex-direction: column;
justify-content: space-between;
gap: 1rem;
}
.postForm label span{
    font-weight: 600;
}
.inputs{
outline: none;
border: .2rem solid rgba(0, 0, 0, 0.199);
width: 100%;
max-width: 250px;
padding: .8rem;
font-family: var(--global-fonts);

}
.inputs:focus{
    border-color:#185585 ;
}
.postForm button{
    width: 100%;
    max-width: 120px;
    margin: auto;
}