.createAccount{
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 2;
}

.createAccount h2{
    font-size: 4rem;
}
.subtitle{
    color: rgba(0, 0, 0, 0.801);
}

.registerForm{
    display: flex;
    flex-direction: column;
    align-items: center;
   margin-top: 3rem;
    gap: .8rem;
    width: 100%;
    max-width: 360px;
}


.registerForm .inputBox{
    display: flex;
    flex-direction: column;
    font-weight: bold;
}


.registerForm .inputsControl{
    padding: .8rem;
    width: 80%;
    min-width: 300px;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.425);
    
    font-family: var(--global-fonts);
    font-weight: 500;
}
.registerForm .inputsControl:focus{
    border: 1px solid #185585;
}
.registerForm button{
    max-width: 120px;
    margin: 2rem auto;


}

.error{
    background-color:#913d16bd;
    padding:0px 5px;
    font-size: 1.4rem;
    color: #fff;
}