.postContainer{
    display: flex;
    flex-direction: column;
    width: 100%;
 
    align-items: center;

}

.postContent{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
    border: 1px solid rgba(0, 0, 0, 0.11);
    padding: 2rem;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    max-width: 35rem;


}

title{
    font-size: 2rem;
}

.postImgBox{
    height: 100%;
    width: 100%;
    max-height: 34rem;
    max-width: 34rem;
}
.postImage{
    width: 100%;
    
    object-fit: cover;
 
    max-height:35rem ;
}
.tagsContainer{
    display: flex;
    gap: .6rem;
}

.tags{
    font-weight: bold;
    border: 1px solid black;
    border-radius: 5px;
    padding: .2rem .3rem;
    font-size: 1.2rem;
   
}


.comment{
   
    max-width: 30rem;
}
.strong{
    font-weight: 500;
   
}

.goHome{
border: 1px solid ;
display: block;
}
.goHome:hover{
    background-color: #000;
    color: #fff;
}