.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 2;
}


.login h2{ font-size: 4rem;}

.login p{
    color: rgba(0, 0, 0, 0.781);
}

.loginForm{
    display: flex;
    flex-direction: column;
    align-items: center;
margin: 3rem auto;
    gap: .8rem;
    width: 100%;
    max-width: 360px;
    
}
.loginForm .inputBox{
    display: flex;
    flex-direction: column;
    gap: .8rem;

}
.loginForm .inputControl{
    padding: .6rem;
    width: 80%;
    min-width: 300px;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.425);

    font-family: var(--global-fonts);
    font-weight: 500;
}
.loginForm .inputControl:focus{
    border: 1px solid #185585;
}
.loginForm button{
    max-width: 120px;
    margin: 2rem auto;


}
.error{
white-space: nowrap;
width: 100%;
text-align: center;
background-color: rgba(255, 0, 0, 0.541);
color: #fff !important;
}